@use '../util' as *;

section.container {
	padding-top: rem(200);

	&:nth-child(5) {
		padding-top: rem(125);
	}

	&:nth-child(6) {
		padding-top: rem(100);
	}
}

.container {
	padding: 0 rem(20);
	max-width: rem(1180);
	margin: 0 auto;

	@include breakpoint(large) {
		transform: translate(0, 0);
		padding: 0;
	}
}
